import React from "react";
import { Paper } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import { Card } from "@material-ui/core";
import { CardContent } from "@material-ui/core";

import DatePicker from "../common/DatePicker";
import SelectPricing from "../common/SelectPricing";
import Table from "../common/Table2";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(3),
  },
  button: {
    marginTop: theme.spacing(3),
    marginRight: theme.spacing(2),
    borderRadius: 50,
    backgroundColor: theme.palette.base1.main,
    color: "#fff",
    "&:hover": {
      backgroundColor: theme.palette.base1.dark,
    },
  },
  card: {
    borderColor: "#F3555E",
    marginTop: theme.spacing(2),
  },
  typo1: {
    color: "#272727",
    textAlign: "center",
    fontWeight: "900",
  },
  typo2: {
    color: "#808080",
    textAlign: "center",
  },
  typo3: {
    color: "#464646",
    textAlign: "left",
    fontSize: "15px",
  },
}));

const transaction = [
  {
    id: 1,
    name: "All",
  },
  {
    id: 2,
    name: "Only Paid",
  },
];

const columns = [
  { id: "timestamp", label: "Timestamp", minWidth: 100 },
  { id: "bookingdetail", label: "Booking Detail", minWidth: 120 },
  { id: "customerdetail", label: "Customer Detail", minWidth: 100 },
  {
    id: "amount",
    label: "Amount/Mode",
    minWidth: 120,
  },
  {
    id: "receipt",
    label: "Receipt",
    minWidth: 120,
  },
  {
    id: "type",
    label: "Type",
    minWidth: 120,
  },
  {
    id: "place",
    label: "Place/Handler",
    minWidth: 120,
  },
];

const List = () => {
  const [formData, setFormData] = React.useState({
    sports: null,
    turf: null,
  });
  const classes = useStyles();
  const styleOthers = (data) => {
    const i = data.indexOf("::");
    if (i >= 0) {
      return (
        <div>
          <Typography className={classes.typo3}>{data.substr(0, i)}</Typography>
          <Typography className={classes.typo3}>{data.substr(i + 2)}</Typography>
        </div>
      );
    } else return <Typography className={classes.typo3}>{data}</Typography>;
  };

  function createData(
    otimestamp,
    obookingdetail,
    ocustomerdetail,
    oamount,
    oreceipt,
    otype,
    oplace
  ) {
    const timestamp = styleOthers(otimestamp);
    const bookingdetail = styleOthers(obookingdetail);
    const customerdetail = styleOthers(ocustomerdetail);
    const amount = styleOthers(oamount);
    const receipt = styleOthers(oreceipt);
    const type = styleOthers(otype);
    const place = styleOthers(oplace);
    return { timestamp, bookingdetail, customerdetail, amount, receipt, type, place };
  }

  const rows = [
    createData(
      "11-Sep-21 :: 4-00-5:00pm",
      "Ref ID: 11733284 :: Football 7-aside",
      "Sunny Mail :: 993234553",
      "Rs 650 :: Cash",
      "29",
      "Cancel",
      "Admin/Playmaker :: Time booked 05-Sep-21 | 09:30 pm"
    ),
    createData(
      "11-Sep-21 :: 4-00-5:00pm",
      "Ref ID: 11733284 :: Football 7-aside",
      "Sunny Mail :: 993234553",
      "Rs 650 :: Cash",
      "29",
      "Cancel",
      "Admin/Playmaker :: Time booked 05-Sep-21 | 09:30 pm"
    ),
  ];
  const SetFormData = (newData) => {
    let newFormData = formData;
    if (newData.sports === "null") {
      newFormData.sports = null;
    } else {
      newData.sports ? (newFormData.sports = newData.sports) : (newFormData.date = newData.date);
    }
    setFormData(newFormData);
  };
  const getSelectValue = (value) => {
    console.log("value :>> ", value);
  };
  return (
    <Grid>
      <Paper className={classes.paper}>
        <Grid container lg={12}>
          <Grid lg={2}>
            <Typography className={classes.typo3} component="div" noWrap>
              From
            </Typography>
            <DatePicker fullWidth={1} changeFormData={(newData) => SetFormData(newData)} />
          </Grid>
          <Grid lg={1}></Grid>
          <Grid lg={2}>
            <Typography className={classes.typo3} component="div" noWrap>
              To
            </Typography>
            <DatePicker fullWidth={1} changeFormData={(newData) => SetFormData(newData)} />
          </Grid>
          <Grid lg={3}>
            <SelectPricing
              data={transaction}
              funct="showt"
              title="Show Transaction"
              getSelectValue={(value) => {
                getSelectValue(value);
              }}
            />
          </Grid>
          <Grid lg={3}>
            <Button variant="outlined" className={classes.button}>
              DOWNLOAD REPORT
            </Button>
          </Grid>
        </Grid>

        <Card variant="outlined" className={classes.card}>
          <CardContent>
            <Grid container lg={12}>
              <Grid lg={1}>
                <Typography variant="h6" className={classes.typo1}>
                  Rs 500
                </Typography>
                <Typography className={classes.typo2}>Cash Paid</Typography>
              </Grid>
              <Grid lg={1}>
                <Typography variant="h6" className={classes.typo1}>
                  Rs 5000
                </Typography>
                <Typography className={classes.typo2}>Card Paid</Typography>
              </Grid>
              <Grid lg={1}>
                <Typography variant="h6" className={classes.typo1}>
                  Rs 0
                </Typography>
                <Typography className={classes.typo2}>Cash Refund</Typography>
              </Grid>
              <Grid lg={1}>
                <Typography variant="h6" className={classes.typo1}>
                  Rs 0
                </Typography>
                <Typography className={classes.typo2}>Card Refund</Typography>
              </Grid>
              <Grid lg={1}>
                <Typography variant="h6" className={classes.typo1}>
                  Rs 0
                </Typography>
                <Typography className={classes.typo2}>Not Cash</Typography>
              </Grid>
              <Grid lg={1}>
                <Typography variant="h6" className={classes.typo1}>
                  Rs 0
                </Typography>
                <Typography className={classes.typo2}>Not Card</Typography>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
        <Table columns={columns} rows={rows} bgWhite={1} />
      </Paper>
    </Grid>
  );
};

export default List;
