import React from "react";
import { useHistory } from "react-router";

import { CardContent } from "@material-ui/core";
import { Card } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { Grid } from "@material-ui/core";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";

import DatePicker from "../common/DatePicker";
import SelectPricing from "../common/SelectPricing";

const useStyles = makeStyles((theme) => ({
  card: {
    padding: theme.spacing(3),
    backgroundColor: "#FCFCFC",
  },
  typo3: {
    color: "#272727",
    paddingBottom: theme.spacing(0.5),
    fontWeight: "bold",
  },
  button: {
    marginTop: theme.spacing(2),
    marginRight: theme.spacing(2),
    borderRadius: 50,
    marginTop: theme.spacing(2),
    backgroundColor: theme.palette.base1.main,
    color: "#fff",
    "&:hover": {
      backgroundColor: theme.palette.base1.dark,
    },
  },
}));

const timePeriod = [
  {
    id: 1,
    name: "Single Day",
  },
  {
    id: 2,
    name: "Week",
  },
];

const transaction = [
  {
    id: 1,
    name: "All",
  },
  {
    id: 2,
    name: "Only Paid",
  },
];

const Index = () => {
  const [formData, setFormData] = React.useState({
    sports: null,
    dateFrom: new Date(),
  });
  const [value, setValue] = React.useState("t");
  const classes = useStyles();
  const history = useHistory();

  const viewReport = () => {
    history.push("/report/list");
  };

  const SetFormData = (newData) => {
    let newFormData = formData;
    if (newData.sports === "null") {
      newFormData.sports = null;
    } else {
      newData.sports ? (newFormData.sports = newData.sports) : (newFormData.date = newData.date);
    }
    setFormData(newFormData);
  };

  const RadioButtons = () => {
    return (
      <FormControl component="fieldset">
        <RadioGroup aria-label="type" name="Type" value={value} onChange={handleChange}>
          <FormControlLabel value="t" control={<Radio />} label="Transaction" />
          <FormControlLabel value="c" control={<Radio />} label="Cancellations" />
        </RadioGroup>
      </FormControl>
    );
  };

  const getSelectValue = (value) => {
    console.log("value :>> ", value);
  };

  const handleChange = (event) => {
    setValue(event.target.value);
  };
  return (
    <Grid>
      <Card className={classes.card} variant="outlined">
        <CardContent>
          <RadioButtons />
        </CardContent>
      </Card>
      <Card className={classes.card} variant="outlined">
        <CardContent>
          <Grid container lg={12}>
            <Grid lg={3}>
              <SelectPricing
                data={timePeriod}
                funct="time"
                title="Time Period"
                getSelectValue={(value) => {
                  getSelectValue(value);
                }}
              />
            </Grid>
            <Grid lg={2}>
              <Typography className={classes.typo3} component="div" noWrap>
                From
              </Typography>
              <DatePicker fullWidth={1} changeFormData={(newData) => SetFormData(newData)} />
            </Grid>
            <Grid lg={1} />
            <Grid lg={2}>
              <Typography className={classes.typo3} component="div" noWrap>
                To
              </Typography>
              <DatePicker fullWidth={1} changeFormData={(newData) => SetFormData(newData)} />
            </Grid>
            <Grid lg={3}>
              <SelectPricing
                data={transaction}
                funct="showt"
                title="Show Transaction"
                getSelectValue={(value) => {
                  getSelectValue(value);
                }}
              />
            </Grid>
          </Grid>
          <Button variant="outlined" className={classes.button}>
            DOWNLOAD REPORT
          </Button>
          <Button onClick={viewReport} variant="outlined" className={classes.button}>
            VIEW REPORT
          </Button>
        </CardContent>
      </Card>
    </Grid>
  );
};

export default Index;
